
<template>
  <div id="app">
    <div id="wrap">
		<h1>{{h1}}</h1>
		<h2>{{h2}}</h2>
		<p>
			<a href="www.baidu.com">{{lajiwu}}</a>
		</p>
		<p>
			<a href="www.baidu.com">{{wan}}</a>
		</p>
		<p>
			<a href="www.baidu.com">{{kan}}</a>
		</p>
	</div>
    <canvas id="can"></canvas>
  </div>
</template>

<script>

window.onload = function() {
  document.addEventListener('touchmove', function (e) {
        e.preventDefault()
    })
    var c = document.getElementsByTagName('canvas')[0],
        x = c.getContext('2d'),
        pr = window.devicePixelRatio || 1,
        w = window.innerWidth,
        h = window.innerHeight,
        f = 90,
        q,
        m = Math,
        r = 0,
        u = m.PI*2,
        v = m.cos,
        z = m.random
    c.width = w*pr
    c.height = h*pr
    x.scale(pr, pr)
    x.globalAlpha = 0.6
    function i(){
        x.clearRect(0,0,w,h)
        q=[{x:0,y:h*.7+f},{x:0,y:h*.7-f}]
        while(q[1].x<w+f) d(q[0], q[1])
    }
    function d(i,j){
        x.beginPath()
        x.moveTo(i.x, i.y)
        x.lineTo(j.x, j.y)
        var k = j.x + (z()*2-0.25)*f,
            n = y(j.y)
        x.lineTo(k, n)
        x.closePath()
        r-=u/-50
        x.fillStyle = '#'+(v(r)*127+128<<16 | v(r+u/3)*127+128<<8 | v(r+u/3*2)*127+128).toString(16)
        x.fill()
		//		x.stroke()
        q[0] = q[1]
        q[1] = {x:k,y:n}
    }
    function y(p){
        var t = p + (z()*2-1.1)*f
        return (t>h||t<0) ? y(p) : t
    }
    document.onclick = i
    document.ontouchstart = i
    i()
}

//360  438


export default {
  name: 'app',
  data(){
    return {
      h1:'AXWE',
    h2:'Maybe nothing',
    lajiwu:'强袭，强无敌',
    kan:'随便看看',
    wan:'随便玩玩'
    }
  }
}
</script>

<style>
canvas{
			position: absolute;
			top: 0;
			left: 0;
			z-index: 0;
			width: 100%;
			height: 100%;
			pointer-events: none;
		}
		#wrap{
			font-family: "微软雅黑";
			position: absolute;
			left: 0;
			width: 320px;
			text-align: center;
			top: 50%;
			left: 50%;
			margin-left: -160px;
			margin-top: -160px;
			-webkit-user-select: none;
			-moz-user-select: none;
			user-select: none;
			z-index: 1;
		}
		a{
			text-decoration:none;
			transition:.3s;
			color:#999;
		}
		a:hover{
			color:red;
		}
</style>



